<template>
    <div>
        <v-card elevation="2" style="margin:0 auto" max-width="500" :loading="loading" :disabled="loading">
            <v-card-text>
                <div class="text-h5 text--primary">輸入UID和驗證碼</div>
                <v-sheet color="white" class="pa-3">
                    <v-form ref="form" v-model="valid" v-on:submit="login">
                        <v-text-field label="UID" v-model="uid" :rules="[v => !!v || '請填入UID']" required></v-text-field>
                        <v-text-field label="驗證碼" v-model="auth" :rules="[v => !!v || '請填入驗證碼']" required></v-text-field>
                        <br>
                        <v-btn depressed block color="primary" @click="login" :loading="loading">登入</v-btn>
                    </v-form>
                </v-sheet>
            </v-card-text>
        </v-card>
        <v-snackbar v-model="snackbar.display">
            <v-chip class="px-3" :color="snackbar.status == 'success' ? 'green' : 'red'" text-color="white" v-if="snackbar.status">
                {{snackbar.status}}
            </v-chip>
            &nbsp;
            {{ snackbar.text }}
            <template v-slot:action="{ attrs }">
                <v-btn color="red" text v-bind="attrs" @click="snackbar.display = false">
                    <v-icon>mdi-close</v-icon>
                </v-btn>
            </template>
        </v-snackbar>
    </div>
</template>

<script>
import constants from '@/constants'

export default {
    name: 'HelloWorld',
    data(){
        return{
            uid: "",
            auth: "",
            loading: false,
            snackbar: {
                display: false,
                text: "test",
                status: null
            },
            valid: false,

        }
    },
    methods:{
        login(){
            if(!this.$refs.form.validate()) return
            this.loading = true
            this.snackbar.display = false
            this.$http.post(constants.TOS_LOGIN_URL + "?uid=" + this.uid + "&auth=" + this.auth).then((res_login) => {
                //console.log(res_login.data)
                this.$http.get(constants.TOS_TOKEN_URL + "?token=" + res_login.data.token).then(() => {
                    //console.log(res.data)
                    this.snackbar.text = "你成功登入了呢，敬請期待"
                    this.snackbar.status = 'success'
                    this.snackbar.display = true
                    this.loading = false
                })
            }).catch((err) => {
                this.snackbar.text = err.response.data.message
                this.snackbar.status = err.response.status
                this.snackbar.display = true
                this.loading = false
            })
        }
    }

}
</script>